import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { UserSandbox } from '../../sandboxes/user.sandbox';

@Injectable({
    providedIn: 'root',
})
export class OraAPIService {

    baseUrl = '';
    basePort = 0;

    userId = '';

    constructor(
        private http: HttpClient,
        public userSandbox: UserSandbox
    ) {
        this.baseUrl = environment.oraBaseUrl;
        this.basePort = environment.oraPort;

        this.userSandbox.userId$.subscribe((id: string) => {
            this.userId = id;
        });
    }

    public setBaseUrl(baseUrl) {
        this.baseUrl = baseUrl;
    }

    public setBasePort(basePort) {
        this.basePort = basePort;
    }

    private handleError(operation: String) {
        return (err: any) => {
            let errMsg = `error in ${operation}()`;
            if (err instanceof HttpErrorResponse) {
                errMsg = `error: ${operation} ${err.status}, ${err.statusText}`;
            }
            return Observable.throw(errMsg);
        };
    }

    private fullUrl(apiPath, value?) {
        return this.baseUrl + String(this.basePort) + apiPath + (value ? value : '');
    }

    public login(email: string, password: string) {
        return this.http.post(this.fullUrl('/login'), {
            email: email,
            password: password
        });
    }

    public logout() {
        return this.http.get(this.fullUrl(`/logout`));
    }

    public register(body) {
        return this.http.post(this.fullUrl(`/register`), body);
    }

    public newCatalog(body) {
        const url = this.fullUrl(`/catalogue`);
        return this.http.post(url, body);
    }

    public updateCatalog(body) {
        const url = this.fullUrl(`/catalogue`);
        return this.http.post(url, body);
    }

    public getAllCatalogs() {
        const url = this.fullUrl(`/merchants/${this.userId}/catalogues`);
        return this.http.get(url);
    }

    public getCatalog(id: string) {
        const url = this.fullUrl(`/catalogue/${id}`);
        return this.http.get(url);
    }

    public deleteOneCatalog(id: string) {
        const url = this.fullUrl(`/catalogue/${id}`);
        return this.http.delete(url);
    }

    public newItem(body) {
        const url = this.fullUrl(`/item`);
        return this.http.post(url, body);
    }

    public updateItem(body) {
        const url = this.fullUrl(`/item`);
        return this.http.post(url, body);
    }

    public getAllItems() {
        const url = this.fullUrl(`/merchants/${this.userId}/items`);
        return this.http.get(url);
    }

    public getCatalogItems(catalogId: string) {
        const url = this.fullUrl(`/item?catalogues=${catalogId}`);
        return this.http.get(url);
    }

    public deleteOneItem(id: string) {
        const url = this.fullUrl(`/item/${id}`);
        return this.http.delete(url);
    }

    public getItemFromUPC(code) {
        return this.http.get(this.fullUrl(`/item/${code}`));
    }

}

