import { Injectable, InjectionToken, Injector } from '@angular/core';
import {
    HttpErrorResponse,
    HttpEvent,
    HttpHandler,
    HttpInterceptor,
    HttpRequest,
    HttpResponse,
    HttpHeaders
} from '@angular/common/http';
import { Events } from '@ionic/angular';
import { Router } from '@angular/router';

import { Observable, EMPTY, throwError, of } from 'rxjs';
import { map, catchError, mergeMap, timeoutWith, takeUntil } from 'rxjs/operators';

import { MessageService } from '../message-service/message.service';
import { HttpCancelService } from '../http-cancel-service/http-cancel.service';
import { UserSandbox } from '../../sandboxes/user.sandbox';

export const DEFAULT_TIMEOUT = new InjectionToken<number>('defaultTimeout');
export const defaultTimeout = 100000;

class TimeoutError extends Error {
    url: string;
    status: number;

    constructor(m: string, url: string) {
        super(m);

        // Set the prototype explicitly.
        Object.setPrototypeOf(this, TimeoutError.prototype);
        this.url = url;
        this.status = -1000;
    }
}

@Injectable({
    providedIn: 'root',
})
export class HttpInterceptorService implements HttpInterceptor {

    private messageService: MessageService;
    private httpCancelService: HttpCancelService;
    private userSandbox: UserSandbox;

    private notificationMap: Map<string, string> = new Map();

    private authToken: string;

    readonly uriWhitelist = ['/login', '/register'];

    constructor(private injector: Injector,
                public events: Events,
                private router: Router,
    ) {
        this.messageService = this.injector.get(MessageService);
        this.httpCancelService = this.injector.get(HttpCancelService);
        this.userSandbox = this.injector.get(UserSandbox);

        this.userSandbox.accessToken$.subscribe((token: string) => {
            this.authToken = token;
        });
    }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        const timeout = Number(request.headers.get('timeout')) || defaultTimeout;
        const notification = request.headers.get('notification') || '';

        // notification header is used for tracking long running requests
        if (notification) {
            this.notificationMap[request.url] = notification;
            this.messageService.broadcast('http', {action: 'start', api: notification});
        }

        if (this.authToken) {
            request = request.clone({ headers: request.headers.set('Authorization', this.authToken) });
        } else {
            const uri = new URL(request.url);
            if (!uri.pathname.startsWith('./assets/i18n')) {
                if (this.uriWhitelist.indexOf(uri.pathname) === -1) {
                    this.events.publish('session:expired');
                    return EMPTY;
                }
            }
        }

        if (!request.headers.has('Content-Type')) {
            request = request.clone({ headers: request.headers.set('Content-Type', 'application/json') });
        }

        const apiErr = new TimeoutError('timeout error', request.url);

        if (!request.headers.has('Authorization')) {
            console.log(`no authorization header on ${request.url}`);
        }
        // response inteceptor
        return next.handle(request).pipe(
            takeUntil(this.httpCancelService.onCancelPendingRequests()),
            timeoutWith(timeout, throwError(apiErr)),
            map((event: HttpEvent<any>) => {
                    if (event instanceof HttpResponse) {
                        // placeholder for examining http response
                        if (this.notificationMap[event.url]) {
                            this.messageService.broadcast('http', {action: 'done', api: this.notificationMap[event.url]});
                        }
                    }
            }),
            // @ts-ignore
            catchError((err: HttpErrorResponse) => {
                if (this.notificationMap[err.url]) {
                    let errMsg = err.message;
                    if (err.status === 401) {
                        errMsg = 'Unauthorized, you must login again';
                    }
                    this.messageService.broadcast('http', {
                        action: 'error',
                        api: this.notificationMap[err.url],
                        err: errMsg
                    });
                }

                if (err.status === 401) {
                    this.events.publish('session:expired');
                    this.router.navigate(['login']);
                }
                throwError(err);
            })
        );
    }
}
