import * as items from './items.model';

type ItemId = string;

export interface ICatalog {
    _id: string;
    name: string, // String of "<merchant name> - product catalogue - <number>"
    description: string,
    created: string,
    lastUpdate: string,
    numItems: number,
    active: boolean // Deletes will be logic, so a user can reactivate a catalogue
    items: Array<items.IItem>
}

export interface ICatalogState {
    catalogs: Array<ICatalog>
    currentCatalog: number;
    currentItem: items.IItem,
    loading: boolean;
    loaded: boolean;
    failed: boolean;
    error: string;
}

export const initialCatalog: ICatalog = {
    _id: '',
    name: '',
    description: '',
    created: '',
    lastUpdate: '',
    numItems: 0,
    active: true,
    items: []
};

export const initialState: ICatalogState = {
    catalogs: [],
    currentCatalog: -1,
    currentItem: items.initialItem,
    loading: false,
    loaded: false,
    failed: false,
    error: ''
};

export const defaultAction = {
    type: 'NO_ACTION'
};

export const getCurrentCatalog = (state: ICatalogState) => {
    if (state.currentCatalog) {
        return state.currentCatalog;
    } else {
        return initialCatalog;
    }
};

export const getCatalogName = (state: ICatalogState) => {
    if (state.currentCatalog > -1) {
        return state.catalogs[state.currentCatalog].name;
    } else {
        return '';
    }
};

export const getCurrentItem = (state: ICatalogState) => state.currentItem;
export const getCatalogs = (state: ICatalogState) => state.catalogs;
export const getLoading = (state: ICatalogState) => state.loading;
export const getLoaded = (state: ICatalogState) => state.loaded;
export const getFailed = (state: ICatalogState) => state.failed;
export const getError = (state: ICatalogState) => state.error;
