/**
 * User State
 */
import { config } from '../../config/app';

export type PermissionsType = any;
export enum UserTypeEnum {
    MERCHANT,
    USER,
    NONE
}

export function USER_TYPE(type: string): UserTypeEnum  {
    if (type === config.server.user.kind.merchant) {
        return UserTypeEnum.MERCHANT;
    } else if (type === config.server.user.kind.user) {
        return UserTypeEnum.USER;
    }
    return UserTypeEnum.NONE;
}

