export interface IUser {
    id: string;
    role: UserTypeEnum;
    firstName: string;
    lastName: string;
    companyName: string;
    email: string;
    mobilePhone: string;
    deviceId: string;
    accessToken: string;
}

export interface IUserState {
    currentUser: IUser;
    loggedIn: boolean;
    loading: boolean;
    loaded: boolean;
    failed: boolean;
    error: string;
}

export const initialCurrentUser = {
    id: "0",
    role: UserTypeEnum.MERCHANT,
    firstName: '',
    lastName: '',
    companyName: 'Merchant Store Name',
    email: 'user@orapay.io',
    mobilePhone: '',
    deviceId: '',
    accessToken: '',
};

export const initialState: IUserState = {
    currentUser: initialCurrentUser,
    loggedIn: false,
    loading: false,
    loaded: false,
    failed: false,
    error: ''
};

export const defaultAction = {
    type: 'NO_ACTION'
};

export const getCurrentUser = (state: IUserState) => state.currentUser;
export const getUserId = (state: IUserState) => state.currentUser.id;
export const getUserName = (state: IUserState) => {
    if (state.currentUser.role === UserTypeEnum.MERCHANT) {
        return state.currentUser.companyName;
    } else if (state.currentUser.role === UserTypeEnum.USER) {
        return state.currentUser.firstName + ' ' + state.currentUser.lastName;
    }
};
export const getUserRole = (state: IUserState) => state.currentUser.role;
export const getAccessToken = (state: IUserState) => state.currentUser.accessToken;
export const getLoggedIn = (state: IUserState) => state.loggedIn;
export const getLoading = (state: IUserState) => state.loading;
export const getLoaded = (state: IUserState) => state.loaded;
export const getFailed = (state: IUserState) => state.failed;
export const getError = (state: IUserState) => state.error;
