import { ActionReducer, MetaReducer } from '@ngrx/store';
import * as Actions from '../actions';
import * as Model from '../models';

import { localStorageSync } from 'ngrx-store-localstorage';
// import { storageSync } from 'ngrx-store-ionic-storage';

export function logout(reducer) {
    return function (state, action) {
        switch (action.type) {
            case Actions.USER_LOGOUT_SUCCESS:
            case Actions.USER_LOGOUT_FAILURE:
                return reducer(undefined);

            default:
                return reducer(state, action);
        }
    };
}

// local storage
export function localStorageSyncWrapper(reducer: ActionReducer<any>): ActionReducer<any> {
    return (localStorageSync({keys: Model.saveStateSet, rehydrate: true, removeOnUndefined: true}))(reducer);
}

export function enableBatchReducer(reduce: ActionReducer<any>): ActionReducer<any> {
    return function batchReducer(state, action: any) {
        if (action.type === Actions.BATCH_ACTION) {
            const batchActions = action.payload;
            return batchActions.reduce(batchReducer, state);
        } else {
            return reduce(state, action);
        }
    };
}

