import { Action } from '@ngrx/store';

import * as UserModel from '../models/user.model';
import * as Actions from '../actions';

import * as JWT from 'jwt-decode';

export function UserReducer(state: UserModel.IUserState = UserModel.initialState, action: Action = UserModel.defaultAction): UserModel.IUserState {
    switch (action.type) {

        case Actions.USER_LOGIN_REQUEST: {
            const name = (<any>action).email;
            return {
                ...state,
                loading: true,
                loaded: false,
                failed: false,
                error: ''
            };
        }

        case Actions.USER_REGISTER_SUCCESS:
        case Actions.USER_LOGIN_SUCCESS: {
            const applicationId = (<any>action).loginResponse.applicationId;
            let user;
            try {
                user = JWT(applicationId);
            } catch(e) {
                console.log(e);
                return {
                    ...state,
                    loading: false,
                    loaded: false,
                    failed: true,
                    error: e
                };
            }
            const currentUser = {
                id: user._id,
                role: UserModel.USER_TYPE(user.kind),
                firstName: user.firstName,
                lastName: user.lastName,
                companyName: user.companyName,
                email: user.email,
                mobilePhone: user.mobilePhone,
                deviceId: user.devices[0]._id,
                accessToken: applicationId,
            };
            return {
                ...state,
                loggedIn: true,
                currentUser: currentUser,
                loading: false,
                loaded: true,
                failed: false
            };
        }

        case Actions.USER_LOGIN_FAILURE: {
            const error = (<Actions.IErrorAction>action).error;
            return {
                ...UserModel.initialState,
                loading: false,
                loaded: false,
                failed: true,
                error: error
            };
        }

        case Actions.USER_LOGOUT_SUCCESS:
        case Actions.USER_LOGOUT_FAILURE: {
            return {
                ...UserModel.initialState,
                loggedIn: false,
            };
        }

        case Actions.USER_REGISTER_REQUEST: {
            return {
                ...state,
                loading: true,
                loaded: false,
                failed: false,
                error: ''
            };
        }

        case Actions.USER_REGISTER_FAILURE: {
            const error = (<Actions.IErrorAction>action).error;
            return {
                ...UserModel.initialState,
                error: error
            };
        }

        case 'NSIS_APP_HYDRATED':
            return {
                ...UserModel.initialState
            };

        default:
            return state;
    }
}



