type IItemId = string;

export interface IItem {
    _id: string;
    name: string;
    description: string;
    upc: string;
    image: string;
    cost: number;
    retail: number;
    merchantId: String, // Needed to have unique versions of products depending on merchants
    catalogs: string[]; // array of Catalog GUID's
}

export interface IItemsState {
    items: Array<IItem>
    currentItem: number,
    loading: boolean;
    loaded: boolean;
    failed: boolean;
    error: string;
}

export const initialItem: IItem = {
    _id: '',
    name: 'New Item',
    description: 'This is a box. It represents the box you could be holding right now',
    upc: '',
    image: 'assets/box-308010.svg',
    cost: 2.50,
    retail: 10.00,
    merchantId: '',
    catalogs: []
};

export const initialState: IItemsState = {
    items: [],
    currentItem: -1,
    loading: false,
    loaded: false,
    failed: false,
    error: ''
};

export const defaultAction = {
    type: 'NO_ACTION'
};

export const getCurrentItem = (state: IItemsState) => {
    if (state.currentItem > -1) {
        return state.items[state.currentItem];
    } else {
        return initialItem;
    }
};

export const getItems = (state: IItemsState) => state.items;
export const getLoading = (state: IItemsState) => state.loading;
export const getLoaded = (state: IItemsState) => state.loaded;
export const getFailed = (state: IItemsState) => state.failed;
export const getError = (state: IItemsState) => state.error;
