import { createSelector } from 'reselect';
import * as model from './models/index';
import { Store } from '@ngrx/store';
import { take } from 'rxjs/operators';

import { IAppState } from './models';

export * from './models/index';
export * from './actions/index';

export * from './reducers/index';

export const getState = (store: Store<IAppState>): IAppState => {
    let state: IAppState;
    store.pipe(take(1)).subscribe(s => state = s);
    return state;
};

export const getUserState = (state: model.IAppState) => state.user;
export const getUserLoaded = createSelector(getUserState, model.User.getLoaded);
export const getUserLoading = createSelector(getUserState, model.User.getLoading);
export const getUserFailed = createSelector(getUserState, model.User.getFailed);
export const getCurrentUser = createSelector(getUserState, model.User.getCurrentUser);
export const getUserId = createSelector(getUserState, model.User.getUserId);
export const getUserName = createSelector(getUserState, model.User.getUserName);
export const getUserRole = createSelector(getUserState, model.User.getUserRole);
export const getAccessToken = createSelector(getUserState, model.User.getAccessToken);
export const getUserError = createSelector(getUserState, model.User.getError);
export const getLoggedIn = createSelector(getUserState, model.User.getLoggedIn);

