import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Events } from '@ionic/angular';

import { Sandbox } from './base.sandbox';
import * as store from '../../store';
import { AuthService } from '../async-services/auth-service/auth.service';

@Injectable({
    providedIn: 'root',
})
export class UserSandbox extends Sandbox {

    public userError$ = this.appState$.select(store.getUserFailed);
    public userLoading$ = this.appState$.select(store.getUserLoading);
    public userLoaded$ = this.appState$.select(store.getUserLoaded);
    public user$ = this.appState$.select(store.getCurrentUser);
    public userName$ = this.appState$.select(store.getUserName);
    public userRole$ = this.appState$.select(store.getUserRole);
    public userErrorMessage$ = this.appState$.select(store.getUserError);
    public accessToken$ = this.appState$.select(store.getAccessToken);
    public isLoggedIn$ = this.appState$.select(store.getLoggedIn);
    public userState$ = this.appState$.select(store.getUserState);
    public userId$ = this.appState$.select(store.getUserId);

    public currentUser: store.User.IUser;
    private loginSubscription = null;

    constructor(protected appState$: Store<store.IAppState>,
                protected authService: AuthService,
                public events: Events
    ) {
        super(appState$);
    }

    // ionic router can only be injected into component, so pass it in for an injectable
    public login(username: string, password: string): void {
        this.appState$.dispatch(store.userLoginRequest(username, password));
        if (!this.loginSubscription) {
            this.listenForLogin();
        }
    }

    public logout(): void {
        this.appState$.dispatch(store.userLogoutRequest());
    }

    public register(registration: Array<string>): void {
        this.appState$.dispatch(store.userRegisterRequest(registration));
        if (!this.loginSubscription) {
            this.listenForLogin();
        }
    }

    public getMerchantId(): string {
        return store.getState(this.appState$).user.currentUser.id;
    }

    /**
     * events
     */
    private listenForLogin(): void {
        // Subscribes to the user logging in, sets the token in the auth service, sends a message that is ok to navigate to main page
        this.loginSubscription = this.accessToken$.subscribe((token: string) => {
            if (token) {
                this.authService.setToken(token);
                this.events.publish('login:success');
                if (this.loginSubscription) {
                    this.loginSubscription.unsubscribe();
                    this.loginSubscription = null;
                }
            }
        });
    }

}
