import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { filter, map } from 'rxjs/operators';
export interface IMessageServiceMessage {
    channel: string;
    message: any;
}

@Injectable()
export class MessageService {
    // private messageSource = new BehaviorSubject<IMessageServiceMessage>({channel: '', message: null});
    private messageSource = new ReplaySubject<IMessageServiceMessage>(10);
    private channel;

    public messageBus = this.messageSource.asObservable();

    constructor() {
    }

    private isMyChannel(m: IMessageServiceMessage, i: number): boolean {
        return m.channel === this.channel;
    }

    public listen(channel: string): Observable<any> {
        this.channel = channel;
        return this.messageBus.pipe(filter(m => m.channel === channel));
    }

    public broadcast(channel: string, message: any) {
        const msg: IMessageServiceMessage = {
            channel: channel,
            message: message
        };
        this.messageSource.next(msg);
    }
}
