import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Sandbox } from './base.sandbox';
import * as store from '../../store';
import { IItem, IItemsState } from '../../store/models/items.model';

@Injectable({
    providedIn: 'root',
})
export class ItemsSandbox extends Sandbox {

    public itemError$ = this.appState$.select(store.getItemError);
    public itemLoading$ = this.appState$.select(store.getItemLoading);
    public itemLoaded$ = this.appState$.select(store.getItemLoaded);
    public items$ = this.appState$.select(store.getItems);
    public itemErrorMessage$ = this.appState$.select(store.getItemError);
    public currentItem$ = this.appState$.select(store.getCurrentItem);

    constructor(protected appState$: Store<store.IAppState>) {
        super(appState$);
        this.registerItemEvents();
    }

    public createNewItem(itemName: string): void {
        this.appState$.dispatch(store.newItemRequest(itemName));
    }

    public getItem(upcCode: string): void {
        this.appState$.dispatch(store.getOneItemRequest(upcCode));
    }

    public setCurrentItemByIndex(index: number): void {
        const state: IItemsState = store.getState(this.appState$).items;
        const currentItem = state.items[index];
        this.appState$.dispatch(store.setCurrentItem(currentItem));
    }

    public setCurrentItem(item: IItem): void {
        this.appState$.dispatch(store.setCurrentItem(item));
    }

    public getItems(): void {
        this.appState$.dispatch(store.getAllItemRequest())
    }

    public getItemsForCatalog(id: string): void {
        this.appState$.dispatch(store.getAllItemsForCatalogRequest(id));
    }

    /**
     * Registers events
     */
    private registerItemEvents(): void {
        // this.subscriptions.push(this.item$.filter(u => u.name !== '').subscribe((item: store.Item.IItem) => {
        //   this.currentItem = item;
        // }));
    }

}
