import { Action } from '@ngrx/store';

import * as Models from '../models';
import * as Actions from '../actions';

export function ItemsReducer(state: Models.Items.IItemsState = Models.Items.initialState,
                               action: Action = Models.Items.defaultAction): Models.Items.IItemsState {
    switch (action.type) {

        case Actions.GET_ALL_CATALOG_ITEMS_REQUEST:
        case Actions.GET_ALL_ITEM_REQUEST:
        case Actions.GET_ONE_ITEM_REQUEST:
        case Actions.DELETE_ITEM_REQUEST:
        case Actions.UPDATE_ITEM_REQUEST:
        case Actions.NEW_ITEM_REQUEST: {
            return {
                ...state,
                loading: true,
                loaded: false,
                failed: false,
                error: ''
            };
        }

        case Actions.NEW_ITEM_SUCCESS: {
            const newItem = (<any>action).newItemResponse;
            const items = state.items.slice(0);
            items.push(newItem);
            return {
                ...state,
                items:  items,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.UPDATE_ITEM_SUCCESS: {
            const newItem = (<any>action).updateItemResponse;
            const items = state.items.slice(0);
            const index = items.findIndex(c => {
                return c._id === newItem._id;
            });
            items[index] = newItem;
            return {
                ...state,
                items: items,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.DELETE_ITEM_SUCCESS: {
            const removedItem = (<any>action).deleteItemResponse;
            const index = state.items.findIndex(c => {
                return c._id === removedItem._id;
            });
            const items = state.items.splice(index, 1);
            return {
                ...state,
                items: items,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ONE_ITEM_SUCCESS: {
            const items = state.items.slice(0);
            const item = (<any>action).getOneItemResponse;
            const index = items.findIndex(c => {
                return c._id === item._id;
            });
            if (index !== -1) {
                items[index] = item;
            } else {
                items.push(item);
            }
            return {
                ...state,
                items: items,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ALL_CATALOG_ITEMS_SUCCESS: {
            const items = (<any>action).getAllItemsForCatalogResponse;
            return {
                ...state,
                items: items,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ALL_ITEM_SUCCESS: {
            const items = (<any>action).getAllItemResponse.items;
            return {
                ...state,
                items: items,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ALL_ITEM_FAILURE:
        case Actions.GET_ONE_ITEM_FAILURE:
        case Actions.DELETE_ITEM_FAILURE:
        case Actions.UPDATE_ITEM_FAILURE:
        case Actions.NEW_ITEM_FAILURE: {
            const error = (<Actions.IErrorAction>action).error;
            return {
                ...Models.Items.initialState,
                loading: false,
                loaded: false,
                failed: true,
                error: error
            };
        }

        case Actions.SET_CURRENT_ITEM: {
            const  itemId = (<any>action).itemId;
            return {
                ...state,
                currentItem: itemId,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        default:
            return state;
    }
}



