import { Action } from '@ngrx/store';

import * as Models from '../models';
import * as Actions from '../actions';

export function CatalogReducer(state: Models.Catalog.ICatalogState = Models.Catalog.initialState,
                               action: Action = Models.Catalog.defaultAction): Models.Catalog.ICatalogState {
    switch (action.type) {

        case Actions.GET_ALL_CATALOG_REQUEST:
        case Actions.GET_ONE_CATALOG_REQUEST:
        case Actions.DELETE_CATALOG_REQUEST:
        case Actions.UPDATE_CATALOG_REQUEST:
        case Actions.NEW_CATALOG_REQUEST: {
            return {
                ...state,
                loading: true,
                loaded: false,
                failed: false,
                error: ''
            };
        }

        case Actions.NEW_CATALOG_SUCCESS: {
            const newCatalog = (<any>action).newCatalogResponse.name;
            const catalogs = state.catalogs.slice(0);
            catalogs.push(newCatalog);
            return {
                ...state,
                catalogs:  catalogs,
                currentCatalog: catalogs.length-1,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.UPDATE_CATALOG_SUCCESS: {
            const newCatalog = (<any>action).updateCatalogResponse;
            const catalogs = state.catalogs.slice(0);
            const index = catalogs.findIndex(c => {
                return c._id === newCatalog._id;
            });
            catalogs[index] = newCatalog;
            return {
                ...state,
                catalogs: catalogs,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.DELETE_ITEM_SUCCESS: {
            const removedCatalog = (<any>action).deleteCatalogResponse;
            const index = state.catalogs.findIndex(c => {
                return c._id === removedCatalog._id;
            });
            const catalogs = state.catalogs.splice(index, 1);
            return {
                ...state,
                catalogs: catalogs,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ONE_CATALOG_SUCCESS: {
            const catalogs = state.catalogs.slice(0);
            const catalog = (<any>action).getOneCatalogResponse;
            const index = catalogs.findIndex(c => {
                return c._id === catalog._id;
            });
            if (index !== -1) {
                catalogs[index] = catalog;
            } else {
                catalogs.push(catalog);
            }
            return {
                ...state,
                catalogs: catalogs,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ALL_CATALOG_SUCCESS: {
            const catalogs = (<any>action).getAllCatalogResponse;
            return {
                ...state,
                catalogs: catalogs,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        case Actions.GET_ALL_CATALOG_FAILURE:
        case Actions.GET_ONE_CATALOG_FAILURE:
        case Actions.DELETE_CATALOG_FAILURE:
        case Actions.UPDATE_CATALOG_FAILURE:
        case Actions.NEW_CATALOG_FAILURE: {
            const error = (<Actions.IErrorAction>action).error;
            return {
                ...Models.Catalog.initialState,
                loading: false,
                loaded: false,
                failed: true,
                error: error
            };
        }

        case Actions.SET_CURRENT_CATALOG: {
            const  catalogId = (<any>action).catalogId;
            return {
                ...state,
                currentCatalog: catalogId,
                loading: false,
                loaded: true,
                failed: false,
                error: ''
            };
        }

        default:
            return state;
    }
}



