import { makeActionCreator } from './base.actions';

export const NEW_CATALOG_REQUEST = '[Catalog] request new';
export const NEW_CATALOG_SUCCESS = '[Catalog] new success';
export const NEW_CATALOG_FAILURE = '[Catalog] new failure';

export interface InewCatalogRequest {
    name?: string; // Optional
}
export interface InewCatalogResponse {
    created: string;
    name: string;
}

export const newCatalogRequest = makeActionCreator(NEW_CATALOG_REQUEST, 'name');
export const newCatalogSuccess = makeActionCreator(NEW_CATALOG_SUCCESS, 'newCatalogResponse');
export const newCatalogFailure = makeActionCreator(NEW_CATALOG_FAILURE, 'error');

export const UPDATE_CATALOG_REQUEST = '[Catalog] request update';
export const UPDATE_CATALOG_SUCCESS = '[Catalog] update success';
export const UPDATE_CATALOG_FAILURE = '[Catalog] update  failure';

export interface IupdateCatalogRequest {
    name: string;
    description: string;
}
export interface IupdateCatalogResponse {
    _id: string; // catalog GUID
    lastUpdate: string;
    name: string;
    description: string;
}

export const updateCatalogRequest = makeActionCreator(UPDATE_CATALOG_REQUEST, 'name', 'description');
export const updateCatalogSuccess = makeActionCreator(UPDATE_CATALOG_SUCCESS, 'updateCatalogResponse');
export const updateCatalogFailure = makeActionCreator(UPDATE_CATALOG_FAILURE, 'error');


export const DELETE_CATALOG_REQUEST = '[Catalog] request delete';
export const DELETE_CATALOG_SUCCESS = '[Catalog] delete success';
export const DELETE_CATALOG_FAILURE = '[Catalog] delete  failure';

export interface IdeleteCatalogResponse {
    _id: string; // catalog GUID
    name: string; // String of "<merchant name> - product catalogue - <number>"
    description: string;
    created: string;
    lastUpdate: string;
    numItems: number;
}

export const deleteCatalogRequest = makeActionCreator(DELETE_CATALOG_REQUEST);
export const deleteCatalogSuccess = makeActionCreator(DELETE_CATALOG_SUCCESS, 'deleteCatalogResponse');
export const deleteCatalogFailure = makeActionCreator(DELETE_CATALOG_FAILURE, 'error');

