import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';

import { AuthService } from './auth-service/auth.service';
import { DEFAULT_TIMEOUT, defaultTimeout, HttpInterceptorService } from './http-intercept/http-intercept.service';
import { OraAPIService } from './ora-api-service/ora.api.service';
import { MessageService } from './message-service/message.service';
import { HttpCancelService } from './http-cancel-service/http-cancel.service';
import { AsyncServicesSandbox } from './async-services.sandbox';

@NgModule({
    imports: [
        CommonModule,
        HttpClientModule
    ],
    declarations: [],
    providers: [
        AsyncServicesSandbox,
        {
            provide: HTTP_INTERCEPTORS,
            useClass: HttpInterceptorService,
            multi: true
        },
        {
            provide: DEFAULT_TIMEOUT,
            useValue: defaultTimeout
        }
    ]
})

export class AsyncServicesModule {
    public static forRoot(): ModuleWithProviders {
        return {
            ngModule: AsyncServicesModule,
            providers: [
                AuthService,
                OraAPIService,
                MessageService,
                HttpCancelService
            ]
        };
    }
}
