export const NEW_ITEM_REQUEST = '[Item] request new';
export const NEW_ITEM_SUCCESS = '[Item] new item success';
export const NEW_ITEM_FAILURE = '[Item] new item  failure';

export interface newItemRequest {
    name: string;
    description: string;
    upc: string;
    cost: number;
    retail: number;
    catalogues: string[];
}
export interface newItemResponse {
    name: string;
    description: string;
    upc: string;
    image: string;
    cost: number;
    retail: number;
    catalogues: string[]; // array of Catalogue GUID's
}

export const newItemRequest = makeActionCreator(NEW_ITEM_REQUEST, 'newItemRequest');
export const newItemSuccess = makeActionCreator(NEW_ITEM_SUCCESS, 'newItemResponse');
export const newItemFailure = makeActionCreator(NEW_ITEM_FAILURE, 'error');

