import { Injectable } from '@angular/core';
import { BarcodeScanner } from '@ionic-native/barcode-scanner/ngx';
import { MessageService } from '../../async-services/message-service/message.service';

@Injectable({
    providedIn: 'root',
})
export class BarcodeScannerService {
    scannerOptions;
    channel = 'BarcodeScan';

    constructor(private barcodeScanner: BarcodeScanner, public messageService: MessageService) {
        this.scannerOptions = {
            showTorchButton: true,
            showFlipCameraButton: true
        }
    }

    public scan(channel?: string) {
        this.barcodeScanner.scan(this.scannerOptions).then(barcodeData => {
           this.messageService.broadcast(channel||this.channel, barcodeData);
        });
    }
}