import { Action } from '@ngrx/store';

export interface IErrorAction extends Action {
    error: string;
}

export function makeActionCreator(type, ...argNames) {
    return function(...args) {
        const action = { type }
        argNames.forEach((arg, index) => {
            action[argNames[index]] = args[index]
        })
        return action
    }
}

export function errorAction(type: string, error: any): IErrorAction {

    let err = 'unknown error';
    if (!error.status === undefined) {
        error.status = -1;
    }

    switch (error.status) {
        case 0:
            err = 'Server not available';
            break;

        case 400:
            err = 'Invalid Username or Password';
            break;

        case 410:
            err = 'Database configuration error: Contact Support';
            break;

        default:
            err = error.message;
    }

    return {
        type: type,
        error: err
    };
}
