import { Injectable, Injector } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { OraAPIService } from '../../services/async-services/ora-api-service/ora.api.service';
import * as actions from '../actions';
import { Action, Store } from '@ngrx/store';
import * as store from '../';

import { Observable, of as observableOf } from 'rxjs';
import { catchError, map, startWith, switchMap, mergeMap } from 'rxjs/operators';

import { AuthService } from '../../services/async-services/auth-service/auth.service';
import { HttpCancelService } from '../../services/async-services/http-cancel-service/http-cancel.service';
import { Location } from '@angular/common';

@Injectable({
    providedIn: 'root',
})
export class UserEffects {
    private authService: AuthService;
    private httpCancelService: HttpCancelService;
    private location: Location;

    constructor(private actions$: Actions,
                private oraService: OraAPIService,
                private injector: Injector,
                protected appState$: Store<store.IAppState>) {
        this.authService = this.injector.get(AuthService);
        this.httpCancelService = this.injector.get(HttpCancelService);
        this.location = this.injector.get(Location);
    }

    @Effect() loginRequest$ = this.actions$
        .pipe(
            ofType(actions.USER_LOGIN_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.login(action.email, action.password)
                .pipe(
                    map((user: any) => (actions.userLoginResponse(user))),
                    catchError((e) => observableOf(actions.errorAction(actions.USER_LOGIN_FAILURE, e)))
                ))
        );

    @Effect() logoutRequest$ = this.actions$
        .pipe(
            ofType(actions.USER_LOGOUT_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.logout()
                .pipe(
                    map((user: any) => {
                        localStorage.clear();
                        return actions.userLogoutSuccess();
                    }),
                    catchError((e) => observableOf(actions.errorAction(actions.USER_LOGOUT_FAILURE, e)))
                ))
        );

    @Effect() registerRequest$ = this.actions$
        .pipe(
            ofType(actions.USER_REGISTER_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.register(action.userRegistrationRequest)
                .pipe(
                    map((user: any) => (actions.userLoginResponse(user))),
                    catchError((e) => observableOf(actions.errorAction(actions.USER_REGISTER_FAILURE, e)))
                ))
        );
}
