import { Action } from '@ngrx/store';
import { makeActionCreator } from './base.actions';

// LOGIN
export const USER_LOGIN_REQUEST = '[User] Login request';
export const USER_LOGIN_SUCCESS = '[User] Login success';
export const USER_LOGIN_FAILURE = '[User] Login failure';

export interface IloginRequest {
    email: string;
    password: string;
}
export interface IloginResponse {
    applicationId: string;
}

export const userLoginRequest = makeActionCreator(USER_LOGIN_REQUEST, 'email', 'password');
export const userLoginResponse = makeActionCreator(USER_LOGIN_SUCCESS, 'loginResponse');
export const userLoginFailure = makeActionCreator(USER_LOGIN_FAILURE, 'error');

// LOGOUT
export const USER_LOGOUT_REQUEST = '[User] Logout request';
export const USER_LOGOUT_SUCCESS = '[User] Logout success';
export const USER_LOGOUT_FAILURE = '[User] Logout failure';

export const userLogoutRequest = makeActionCreator(USER_LOGOUT_REQUEST);
export const userLogoutSuccess = makeActionCreator(USER_LOGOUT_SUCCESS);
export const userLogoutError = makeActionCreator(USER_LOGOUT_FAILURE);

// Register
export const USER_REGISTER_REQUEST = '[User] Register request';
export const USER_REGISTER_SUCCESS = '[User] Register success';
export const USER_REGISTER_FAILURE = '[User] Register failure';

export interface IuserRegistrationRequest {
    email: string;
    password: string;
    firstName: string;
    lastName: string;
    companyName: string;
}

export interface IuserRegisterResponse {
    applicationId: string;
}

export const userRegisterRequest = makeActionCreator(USER_REGISTER_REQUEST, 'userRegistrationRequest');
export const userRegisterSuccess = makeActionCreator(USER_REGISTER_SUCCESS, 'userRegisterResponse');
export const userRegisterFailure = makeActionCreator(USER_REGISTER_FAILURE, 'error');

