import { Injectable, Injector } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { OraAPIService } from '../../services/async-services/ora-api-service/ora.api.service';
import { Action, Store } from '@ngrx/store';
import { IItem } from '../models/items.model';
import * as store from '../';
import * as actions from '../actions';

import { Observable, of as observableOf } from 'rxjs';
import { catchError, map, startWith, switchMap, mergeMap } from 'rxjs/operators';

import { AuthService } from '../../services/async-services/auth-service/auth.service';
import { HttpCancelService } from '../../services/async-services/http-cancel-service/http-cancel.service';

@Injectable({
    providedIn: 'root',
})
export class ItemEffects {
    private authService: AuthService;
    private httpCancelService: HttpCancelService;

    constructor(private actions$: Actions,
                private oraService: OraAPIService,
                private injector: Injector,
                protected appState$: Store<store.IAppState>) {
        this.authService = this.injector.get(AuthService);
        this.httpCancelService = this.injector.get(HttpCancelService);
    }

    @Effect() newItemRequest$ = this.actions$
        .pipe(
            ofType(actions.NEW_ITEM_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.newItem(action.newItemRequest)
                .pipe(
                    map((item: IItem) => (actions.newItemSuccess(item))),
                    catchError((e) => observableOf(actions.errorAction(actions.GET_ALL_CATALOG_FAILURE, e)))
                ))
        );

    @Effect() updateItemRequest$ = this.actions$
        .pipe(
            ofType(actions.UPDATE_ITEM_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.updateItem(action.updateItemRequest)
                .pipe(
                    map((item: IItem) => (actions.updateItemSuccess(item))),
                    catchError((e) => observableOf(actions.errorAction(actions.UPDATE_ITEM_FAILURE, e)))
                ))
        );

    @Effect() deleteItemRequest$ = this.actions$
        .pipe(
            ofType(actions.DELETE_ITEM_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.deleteOneItem(action.id)
                .pipe(
                    map((item: IItem) => (actions.deleteItemSuccess(item))),
                    catchError((e) => observableOf(actions.errorAction(actions.DELETE_ITEM_FAILURE, e)))
                ))
        );

    @Effect() getOneItemRequest$ = this.actions$
        .pipe(
            ofType(actions.GET_ONE_ITEM_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.getItemFromUPC(action.code)
                .pipe(
                    map((item: IItem) => (actions.getOneItemSuccess(item))),
                    catchError((e) => observableOf(actions.errorAction(actions.GET_ONE_ITEM_FAILURE, e)))
                ))
        );

    @Effect() getAllItemRequest$ = this.actions$
        .pipe(
            ofType(actions.GET_ALL_ITEM_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.getAllItems()
                .pipe(
                    map((item: IItem) => (actions.getAllItemSuccess(item))),
                    catchError((e) => observableOf(actions.errorAction(actions.GET_ALL_ITEM_FAILURE, e)))
                ))
        );

    @Effect() getAllItemsForCatalogRequest$ = this.actions$
        .pipe(
            ofType(actions.GET_ALL_CATALOG_ITEMS_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.getCatalogItems(action.id)
                .pipe(
                    map((items: Array<IItem>) => (actions.getAllItemsForCatalogSuccess(items))),
                    catchError((e) => observableOf(actions.errorAction(actions.GET_ALL_CATALOG_ITEMS_FAILURE, e)))
                ))
        );
}
