import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Sandbox } from './base.sandbox';
import * as store from '../../store';
import { ICatalogState, ICatalog } from '../../store/models/catalog.model';

@Injectable({
    providedIn: 'root',
})
export class CatalogSandbox extends Sandbox {

    public catalogError$ = this.appState$.select(store.getCatalogError);
    public catalogLoading$ = this.appState$.select(store.getCatalogLoading);
    public catalogLoaded$ = this.appState$.select(store.getCatalogLoaded);
    public catalogs$ = this.appState$.select(store.getCatalogs);
    public catalogName$ = this.appState$.select(store.getCatalogName);
    public catalogErrorMessage$ = this.appState$.select(store.getCatalogError);
    public currentItem$ = this.appState$.select(store.getCurrentItem);
    public currentCatalog$ = this.appState$.select(store.getCurrentCatalog);

    constructor(protected appState$: Store<store.IAppState>) {
        super(appState$);
        this.registerCatalogEvents();
    }

    public createNewCatalog(catalogName: string): void {
        this.appState$.dispatch(store.newCatalogRequest(catalogName));
    }

    public setCurrentCatalogByIndex(index: number): void {
        const state: ICatalogState = store.getState(this.appState$).catalogs;
        const currentCatalog = state.catalogs[index];
        this.appState$.dispatch(store.setCurrentCatalog(currentCatalog));
    }

    public setCurrentCatatlog(catalog: ICatalog): void {
        this.appState$.dispatch(store.setCurrentCatalog(catalog));
    }

    public getCatalogs(): void {
        this.appState$.dispatch(store.getAllCatalogRequest())
    }

    /**
     * Registers events
     */
    private registerCatalogEvents(): void {
        // this.subscriptions.push(this.catalog$.filter(u => u.name !== '').subscribe((catalog: store.Catalog.ICatalog) => {
        //   this.currentCatalog = catalog;
        // }));
    }

}
