import { Injectable, Injector } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { OraAPIService } from '../../services/async-services/ora-api-service/ora.api.service';
import { Action, Store } from '@ngrx/store';
import * as actions from '../actions';
import * as store from '../';

import { of as observableOf } from 'rxjs';
import { catchError, map, switchMap, mergeMap } from 'rxjs/operators';

import { AuthService } from '../../services/async-services/auth-service/auth.service';
import { HttpCancelService } from '../../services/async-services/http-cancel-service/http-cancel.service';
import { ICatalog } from '../models/catalog.model';

@Injectable({
    providedIn: 'root',
})
export class CatalogEffects {
    private authService: AuthService;
    private httpCancelService: HttpCancelService;

    constructor(private actions$: Actions,
                private oraService: OraAPIService,
                private injector: Injector,
                protected appState$: Store<store.IAppState>) {
        this.authService = this.injector.get(AuthService);
        this.httpCancelService = this.injector.get(HttpCancelService);
    }

    @Effect() newCatalogRequest$ = this.actions$
        .pipe(
            ofType(actions.NEW_CATALOG_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.newCatalog({name: action.name})
                .pipe(
                    map((catalog: ICatalog) => (actions.newCatalogSuccess(catalog))),
                    catchError((e) => observableOf(actions.errorAction(actions.NEW_CATALOG_FAILURE, e)))
                )
        ));

    @Effect() updateCatalogRequest$ = this.actions$
        .pipe(
            ofType(actions.UPDATE_CATALOG_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.updateCatalog(action.id)
                .pipe(
                    map((catalog: ICatalog) => (actions.updateCatalogSuccess(catalog))),
                    catchError((e) => observableOf(actions.errorAction(actions.UPDATE_CATALOG_FAILURE, e)))
                ))
        );

    @Effect() deleteCatalogRequest$ = this.actions$
        .pipe(
            ofType(actions.DELETE_CATALOG_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.deleteOneCatalog(action.id)
                .pipe(
                    map((catalog: ICatalog) => (actions.deleteCatalogSuccess(catalog))),
                    catchError((e) => observableOf(actions.errorAction(actions.DELETE_CATALOG_FAILURE, e)))
                ))
        );


    @Effect() getOneCatalogRequest$ = this.actions$
        .pipe(
            ofType(actions.GET_ONE_CATALOG_REQUEST),
            switchMap((action: actions.IGenericAction) => this.oraService.getCatalog(action.id)
                .pipe(
                    map((catalog: ICatalog) => (actions.getOneCatalogSuccess(catalog))),
                    catchError((e) => observableOf(actions.errorAction(actions.GET_ONE_CATALOG_FAILURE, e)))
                ))
        );

    @Effect() getAllCatalogRequest$ = this.actions$
        .pipe(
            ofType(actions.GET_ALL_CATALOG_REQUEST),
            switchMap(() => this.oraService.getAllCatalogs()
                .pipe(
                    map((catalog: ICatalog) => (actions.getAllCatalogSuccess(catalog))),
                    catchError((e) => observableOf(actions.errorAction(actions.GET_ALL_CATALOG_FAILURE, e)))
                ))
        );
}
